<template lang="html">
  <div class="param">
    <div class="param-wrapper">
      <h1 class="title">
        M8
      </h1>
      <div class="param-box">
        <swiper :options="swiperOption4" ref="mySwiper">
          <swiper-slide>
            <img src="http://img1.app.meitudata.com/meitushop/v4/pc_img/m8/size-5.2.png?v=201705021910" alt="">
          </swiper-slide>
          <swiper-slide>
            <img src="http://img1.app.meitudata.com/meitushop/v4/pc_img/m8/size-5.15.png?v=201705021910" alt="">
          </swiper-slide>
          <div class="swiper-pagination"  slot="pagination"></div>
        </swiper>
        <div class="param-content">
          <div class="content">
            <h3 class="subtitle"></h3>
            <div class="clearfix">
      				<label class="paramter-name"></label>
      				<div class="paramter-value">M8</div>
      			</div>
            <div class="clearfix">
      				<label class="paramter-name"></label>
      				<div class="paramter-value">25992799Hello Kitty</div>
      			</div>
            <div class="clearfix">
      				<label class="paramter-name"></label>
      				<div class="paramter-value"></div>
      			</div>
            <div class="clearfix">
      				<label class="paramter-name"></label>
      				<div class="paramter-value">   </div>
      			</div>
          </div>
          <div class="content">
            <h3 class="subtitle">cpu,</h3>
            <div class="clearfix">
      				<label class="paramter-name">CPU</label>
      				<div class="paramter-value">MT6797m 2.1GHz</div>
      			</div>
            <div class="clearfix">
      				<label class="paramter-name">GPU</label>
      				<div class="paramter-value">MALI T880</div>
      			</div>
            <div class="clearfix">
      				<label class="paramter-name"></label>
      				<div class="paramter-value">MEIOS3.6Android M</div>
      			</div>
            <div class="clearfix">
      				<label class="paramter-name">RAM</label>
      				<div class="paramter-value">4GB</div>
      			</div>
            <div class="clearfix">
      				<label class="paramter-name">ROM</label>
      				<div class="paramter-value">64G</div>
      			</div>
            <div class="clearfix">
      				<label class="paramter-name"></label>
      				<div class="paramter-value">3000mAh </div>
      			</div>
            <div class="clearfix">
      				<label class="paramter-name">SIM</label>
      				<div class="paramter-value">Nano-SIM</div>
      			</div>
          </div>
          <div class="content">
            <h3 class="subtitle"></h3>
            <div class="clearfix">
      				<label class="paramter-name"></label>
      				<div class="paramter-value">1200DualPDSONY IMX362</div>
      			</div>
            <div class="clearfix">
      				<label class="paramter-name"></label>
      				<div class="paramter-value">2100PDAFSONY IMX230</div>
      			</div>
            <div class="clearfix">
      				<label class="paramter-name"></label>
      				<div class="paramter-value">Milbeaut</div>
      			</div>
            <div class="clearfix">
      				<label class="paramter-name"></label>
      				<div class="paramter-value"></div>
      			</div>
          </div>
        </div>
      </div>
      <footBar></footBar>
    </div>
  </div>
</template>

<script>
import footBar from '../public/footer'
export default {
  data() {
    return {
      swiperOption4: {
        autoplay: 3000,
        initialSlide: 1,
        loop: true,
        paginationClickable :true,
        pagination: '.swiper-pagination',
      },
    }
  },
  components:{
    footBar
  }
}
</script>

<style lang="css" scoped>
.param-wrapper {
  width: 100%;
  background: #1e1b1a;
  padding-top: 2rem;
  box-sizing: border-box;
}
.param-wrapper .title {
  font-size: 2rem;
  margin-bottom: 2rem;
  text-align: center;
}
.param-content .content .subtitle {
  color: #fff;
  font-size: 1.6rem;
  padding: 0 10px;
  background-color: #2A2929;
  box-sizing: border-box;
  line-height: 1.5;
  margin-bottom: 1rem;
  margin-top: 1rem;
}
.clearfix {
   color: #acabac;
   font-size: 1.4rem;
   position: relative;
   display: flex;
   line-height: 2rem;
}
.clearfix .paramter-name {
  width: 8rem;
  text-align: right;
  margin-right: 2rem;
}
 .clearfix .paramter-value {

 }
 .clearfix:after {
   clear: both;
 }
 .param-box {
   width: 100%;
   margin-bottom: 3rem;
 }
 .swiper-pagination-bullet {
   width: 0.8rem;
   height: 0.8rem;
   background-color: #fff;
 }
</style>



